.btn{
  height: auto;
  line-height: 35px;
  padding: 5px 15px;
  text-transform: uppercase;
  font-size: 1.063rem;
  border-radius: 3px;
  white-space: normal;
  border: none;
}

//set buttons colours
.btn-software{
  background-color: $software-color;
  color: $w-color;
}
.btn-support{
  background-color: $support-color;
  color: $w-color;
}
.btn-marketing{
  background-color: $maraketing-color;
  color: $w-color;
}
.btn-telecom{
  background-color: $telecom-color;
  color: $w-color;
}
.btn-design{
