  color: $w-color;
}
.btn-course{
  background-color: $course-color;
  color: $w-color;
}


////////////////////////////////////////
//hamburger sidenav buttons


.ham-button {
  display: inline-block;
  cursor: pointer;
  background-color: $b-color;
  border-radius: 3px;
  padding: 10px;
  margin: 10px;
}

.ham1, .ham2, .ham3 {
  width: 35px;
  height: 5px;
  background-color: $w-color;
  margin: 6px 0;
  transition: 0.4s;
}

.change .ham1 {
  transform: translate(0, 11px) rotate(-45deg);
}

.change .ham2 {opacity: 0;}

.change .ham3 {
  transform: translate(0, -11px) rotate(45deg);
}


///////////////////////////////////////
.nav-btn{
  width: 140px;
}
//switch main navigation icons + background colour
.software span>i{
  color: $software-color;
}
.software:hover{
  background-color: $software-color;
  &::after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 35px 70px 0 70px;
    border-color: #67809f transparent transparent transparent;
    z-index: 2;
    transform: translateX(-70px);
  }
  i{
    color: inherit;
  }
}
